
.tab-container{
    width: 100%;
}
.invisible{
        visibility:hidden;
}
.tabswiper {
      position: relative;
      overflow: hidden;
}
.tabswiper-wrap {
    display: flex;
    display: inline-flex;
    width: 100%;
    height: 100%;
    transition: all 0.2s ease;
    flex-direction: row;
}
.tabswiper-wrap.dragging{
    transition: none;
}
.tabswiper-wrap> div {
  overflow-x:hidden; 
  flex-shrink: 0;
  width: 100%;
  margin: 0px;
  padding: 0px;
}
::-webkit-scrollbar  
{  
    width: 0px;
}
.tab-title-container{
    position: relative;
    /*display: table;*/
    margin: 0 auto;
    list-style: none;
    border-bottom: 1px solid #dddddd;
    display: -webkit-box;
    display: -moz-box;
    display: -o-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.tab-title{
    height: 35px;
    line-height: 35px;
    position: relative;
    text-align: center;
    cursor: pointer;
    outline-style: none;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    /*    display: block;*/
}
.tab-title.active,.tab-title:active {
    border-bottom: 2px solid #36acf4;
    color: #36acf4;
}



